import React, { useState } from 'react';
import Navbar from './Navbar';

const Layout = ({ children }) => {

  return (
    <div className="min-h-screen bg-white overflow-hidden">
        <div className="p-8 md:p-20 w-full md:max-w-[25%]">
          <Navbar />
          <main className="mt-8">
            {children}
          </main>
        </div>
    </div>
  );
};

export default Layout;