import { useQuery } from '@tanstack/react-query';
import { strapiService } from '../services/api';

const defaultOptions = {
  staleTime: 5 * 60 * 1000,
  cacheTime: 30 * 60 * 1000,
};

export const useStrapiData = (queryKey, fetchFunction, options = {}) => {
  return useQuery({
    queryKey: [queryKey],
    queryFn: fetchFunction,
    ...defaultOptions,
    ...options,
  });
};

export const useInfoData = (options) => 
  useStrapiData('info', strapiService.getInfo, options);

export const useHomeData = (options) => 
  useStrapiData('home', strapiService.getHomePage, options);

export const useGlobalData = (options) => 
  useStrapiData('global', strapiService.getGlobalData, options);

export const useAwardsData = () => {
  return useStrapiData('awards', strapiService.getAwards);
};