import React from 'react';
import { Link } from 'react-router-dom';
import { useInfoData } from '../hooks/useStrapiData';
import LoadingSpinner from '../components/common/LoadingSpinner';
import InfoItem from '../components/InfoItem';
import ErrorView from '../components/common/ErrorView';
import PageLayout from '../components/layout/PageLayout';
import { ROUTES } from '../constants';

const Info = () => {
  const { data: response, isLoading, error, refetch } = useInfoData();
  const infoData = response?.data || [];

  if (isLoading) return <LoadingSpinner size="lg" />;
  if (error) return <ErrorView message={error.message} onRetry={refetch} />;

  return (
    <PageLayout>
      <div className="space-y-6">
        {infoData.map((item) => (
          <div key={item.id}>
            {item.Info.map((infoItem) => (
              <InfoItem
                key={infoItem.id}
                year={infoItem.Year}
                title={infoItem.Title}
                subtitle={infoItem.SubTitle}
              />
            ))}
          </div>
        ))}
      </div>
      
      <div className="flex flex-col space-y-2 mt-16">
        <div className="flex items-center text-[1rem] text-black">
          <span className="mr-2"></span>
          <Link to="/awards" className="hover:text-[#6366f1]">Awards</Link>
        </div>
        <div className="flex items-center text-[1rem] text-black">
          <span className="mr-2"></span>
          <Link to="/" className="hover:text-[#6366f1]">Back</Link>
        </div>
      </div>
    </PageLayout>
  );
};

export default Info;