import React from 'react';
import { Link } from 'react-router-dom';
import DrumMachine from '../components/DrumMachine';
import useWindowResize from '../hooks/useWindowResize';
import { useHomeData } from '../hooks/useStrapiData';
import { BlocksRenderer } from '@strapi/blocks-react-renderer';
import LoadingSpinner from '../components/common/LoadingSpinner';

const Home = () => {
  const { windowSize, baseUnit, mobileInfo } = useWindowResize();
  const { data: response, isLoading, error } = useHomeData();
  
  const homeData = response?.data || null;

  if (isLoading) {
    return <LoadingSpinner size="lg" />;
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center p-6 bg-red-50 rounded-lg max-w-md">
          <h3 className="text-red-600 font-medium mb-2">Error Loading Content</h3>
          <p className="text-red-500 mb-4">{error.message}</p>
          <button 
            onClick={() => window.location.reload()} 
            className="px-4 py-2 bg-red-100 text-red-700 rounded hover:bg-red-200 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  return (
    <div 
      className="flex flex-col"
      style={{ 
        '--base-unit': `${baseUnit}px`,
        minHeight: `${windowSize.height}px`
      }}
    >
      <div className="mb-16">
        {homeData?.Content ? (
          <div className="prose max-w-xl">
            <BlocksRenderer content={homeData.Content} />
          </div>
        ) : (
          <p className="text-[1rem] text-gray-900 max-w-xl">
            No content available
          </p>
        )}
      </div>

      <nav className="mb-16">
        <div className="flex flex-col space-y-2">
          {homeData?.Menu?.map((menuItem) => (
            <div key={menuItem.id} className="flex items-center text-[1rem] text-gray-600">
              <span className="mr-2"></span>
              <Link 
                to={`/${menuItem.Link}`} 
                className="hover:text-[#6366f1]"
              >
                {menuItem.Name}
              </Link>
            </div>
          ))}
        </div>
      </nav>

      <section className={mobileInfo.active ? 'transform scale-90' : ''}>
        <DrumMachine />
      </section>
    </div>
  );
};

export default Home;