import React from 'react';
import PropTypes from 'prop-types';

const InfoItem = ({ year, title, subtitle }) => (
  <div className="mb-8">
    <p className="text-[1rem] text-black mb-2">{year}</p>
    <h3 className="text-[1rem] text-black mb-2">{title}</h3>
    <p className="text-[1rem] text-black">{subtitle}</p>
  </div>
);

InfoItem.propTypes = {
  year: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
};

export default InfoItem;